import "./globals.css";
import Navbar from "@/components/Navbar";
import { usePathname } from "next/navigation";

export const metadata = {
  title: "The Urban Forge",
  description: "Delicious meals delivered fast.",
  manifest: "/manifest.json",
  themeColor: "#1d1d1d",
};

// Convert layout into a client component to use usePathname
export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <body className="bg-[#1d1d1d] text-white">
        <ClientLayout>{children}</ClientLayout>
      </body>
    </html>
  );
}

// ✅ Client-side layout wrapper
"use client";
function ClientLayout({ children }) {
  const pathname = usePathname();

  // ✅ Only show Navbar on specific pages
  const showNavbar = ["/home", "/orders", "/settings"].includes(pathname);

  return (
    <div className="relative min-h-screen">
      {children}
      {showNavbar && <Navbar />}
    </div>
  );
}
